<template>
  <div class="main-container">
    <div class="tool-header">
      <h1>üéÆ Ê∏∏ÊàèÁéãÁîüÂëΩÂÄºÁªüËÆ°Âô®</h1>
      <p>ËÆ∞ÂΩïÊ∏∏ÊàèÁéãÂÜ≥Êñó‰∏≠ÁöÑÁîüÂëΩÂÄºÂèòÂåñÔºåÊîØÊåÅÂ§ö‰∫∫ÂØπÊàòÂíåÂéÜÂè≤ËÆ∞ÂΩï</p>
    </div>

    <div class="game-controls">
      <el-button type="primary" @click="startNewGame">
        <i class="el-icon-refresh"></i> Êñ∞Ê∏∏Êàè
