      <el-button @click="resetCurrentGame">
        <i class="el-icon-refresh-left"></i> ÈáçÁΩÆÂΩìÂâçÂ±Ä
      </el-button>
      <el-button @click="showHistory = true">
        <i class="el-icon-time"></i> ÂéÜÂè≤ËÆ∞ÂΩï
      </el-button>
      <el-button @click="showSettings = true">
        <i class="el-icon-setting"></i> ËÆæÁΩÆ
      </el-button>
    </div>

    <el-row :gutter="20">
      <el-col 
        v-for="(player, index) in players" 
        :key="index"
        :xs="24" 
        :sm="12" 
        :md="players.length <= 2 ? 12 : 8" 
        :lg="players.length <= 2 ? 12 : 6"
      >
        <el-card class="player-card" :class="{ 'low-life': player.currentLife <= 1000 }">
          <div slot="header" class="player-header">
            <div class="player-info">
              <el-input 
                v-model="player.name" 
                size="small" 
                placeholder="Áé©ÂÆ∂ÂêçÁß∞"
                @blur="saveGameData"
              ></el-input>
              <div class="player-stats">
                ËÉú: {{ player.wins }} | Ë¥ü: {{ player.losses }}
              </div>
            </div>
            <el-dropdown @command="(command) => handlePlayerAction(index, command)">
              <el-button size="mini" type="text">
                <i class="el-icon-more"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="reset">ÈáçÁΩÆÁîüÂëΩÂÄº</el-dropdown-item>
                <el-dropdown-item command="remove" v-if="players.length > 2">ÁßªÈô§Áé©ÂÆ∂</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>

          <div class="life-display">
            <div class="current-life" :class="getLifeClass(player.currentLife)">
              {{ player.currentLife }}
            </div>
            <div class="life-bar">
              <el-progress 
                :percentage="getLifePercentage(player.currentLife)" 
                :status="getLifeStatus(player.currentLife)"
                :show-text="false"
              ></el-progress>
            </div>
          </div>

          <div class="life-controls">
            <div class="quick-actions">
              <el-button 
                v-for="amount in quickAmounts" 
                :key="amount"
                size="mini" 
                type="danger" 
                @click="changeLife(index, -amount)"
              >
                -{{ amount }}
              </el-button>
            </div>
            
            <div class="custom-change">
              <el-input-number 
                v-model="customAmount" 
                size="small" 
                :min="1" 
                :max="99999"
                placeholder="Ëá™ÂÆö‰πâÊï∞ÂÄº"
              ></el-input-number>
              <div class="custom-buttons">
                <el-button size="small" type="success" @click="changeLife(index, customAmount)">
                  +{{ customAmount }}
                </el-button>
                <el-button size="small" type="danger" @click="changeLife(index, -customAmount)">
                  -{{ customAmount }}
                </el-button>
              </div>
            </div>

            <div class="quick-actions">
              <el-button 
                v-for="amount in quickAmounts" 
                :key="amount"
                size="mini" 
                type="success" 
                @click="changeLife(index, amount)"
              >
                +{{ amount }}
              </el-button>
            </div>
          </div>

          <div class="life-history">
            <h4>ÂèòÂåñËÆ∞ÂΩï</h4>
            <div class="history-list">
              <div 
                v-for="(record, recordIndex) in player.history.slice(-5)" 
                :key="recordIndex"
                class="history-item"
              >
                <span class="history-change" :class="record.change > 0 ? 'positive' : 'negative'">
                  {{ record.change > 0 ? '+' : '' }}{{ record.change }}
                </span>
                <span class="history-result">‚Üí {{ record.newLife }}</span>
                <span class="history-time">{{ formatTime(record.time) }}</span>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <div class="add-player" v-if="players.length < 4">
      <el-button type="dashed" @click="addPlayer" icon="el-icon-plus">
        Ê∑ªÂä†Áé©ÂÆ∂ ({{ players.length }}/4)
      </el-button>
    </div>

    <div class="game-info">
      <el-card>
        <div slot="header">
          <span>Ê∏∏Êàè‰ø°ÊÅØ</span>
        </div>
        <div class="game-stats">
          <div class="stat-item">
            <strong>Ê∏∏ÊàèÊó∂Èïø:</strong> {{ formatDuration(gameDuration) }}
          </div>
          <div class="stat-item">
            <strong>ÊÄªÂõûÂêàÊï∞:</strong> {{ totalTurns }}
          </div>
          <div class="stat-item">
            <strong>ÂºÄÂßãÊó∂Èó¥:</strong> {{ formatDateTime(gameStartTime) }}
          </div>
        </div>
      </el-card>
    </div>

    <!-- ÂéÜÂè≤ËÆ∞ÂΩïÂØπËØùÊ°Ü -->
    <el-dialog title="ÂéÜÂè≤ËÆ∞ÂΩï" :visible.sync="showHistory" width="80%">
      <div class="history-content">
        <div v-if="gameHistory.length === 0" class="no-history">
          <p>ÊöÇÊó†ÂéÜÂè≤ËÆ∞ÂΩï</p>
        </div>
        <div v-else>
          <div v-for="(game, index) in gameHistory" :key="index" class="history-game">
            <div class="game-header">
              <h4>Ê∏∏Êàè #{{ game.id }}</h4>
              <span class="game-date">{{ formatDateTime(game.startTime) }}</span>
              <el-button size="mini" type="danger" @click="deleteGameHistory(index)">Âà†Èô§</el-button>
            </div>
            <div class="game-summary">
              <div class="game-duration">Êó∂Èïø: {{ formatDuration(game.duration) }}</div>
              <div class="game-players">
                <span v-for="player in game.players" :key="player.name" class="player-summary">
                  {{ player.name }}: {{ player.finalLife }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div slot="footer">
        <el-button @click="clearAllHistory" type="danger">Ê∏ÖÁ©∫ÊâÄÊúâËÆ∞ÂΩï</el-button>
        <el-button @click="showHistory = false">ÂÖ≥Èó≠</el-button>
      </div>
    </el-dialog>

    <!-- ËÆæÁΩÆÂØπËØùÊ°Ü -->
    <el-dialog title="Ê∏∏ÊàèËÆæÁΩÆ" :visible.sync="showSettings" width="60%">
      <el-form label-width="120px">
        <el-form-item label="ÂàùÂßãÁîüÂëΩÂÄº">
          <el-input-number v-model="settings.initialLife" :min="1000" :max="99999"></el-input-number>
        </el-form-item>
        <el-form-item label="Âø´Êç∑Êï∞ÂÄº">
          <el-input v-model="quickAmountsStr" placeholder="Áî®ÈÄóÂè∑ÂàÜÈöîÔºåÂ¶Ç: 100,500,1000"></el-input>
        </el-form-item>
        <el-form-item label="‰ΩéÁîüÂëΩÂÄºË≠¶Âëä">
          <el-input-number v-model="settings.lowLifeThreshold" :min="0" :max="5000"></el-input-number>
        </el-form-item>
        <el-form-item label="Èü≥ÊïàÊèêÁ§∫">
          <el-switch v-model="settings.soundEnabled"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="resetSettings">ÊÅ¢Â§çÈªòËÆ§</el-button>
        <el-button type="primary" @click="saveSettings">‰øùÂ≠òËÆæÁΩÆ</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'YugiohLifeView',
  metaInfo: {
    title: 'Ê∏∏ÊàèÁéãÁîüÂëΩÂÄºÁªüËÆ°Âô® - ÂÜ≥ÊñóÁîüÂëΩÂÄºËÆ∞ÂΩïÂ∑•ÂÖ∑',
    meta: [
      { name: 'description', content: 'Ê∏∏ÊàèÁéãÂÜ≥ÊñóÁîüÂëΩÂÄºÁªüËÆ°Â∑•ÂÖ∑ÔºåÊîØÊåÅÂ§ö‰∫∫ÂØπÊàò„ÄÅÂéÜÂè≤ËÆ∞ÂΩï„ÄÅËá™ÂÆö‰πâËÆæÁΩÆÔºåÊï∞ÊçÆÊú¨Âú∞‰øùÂ≠ò„ÄÇ' },
      { name: 'keywords', content: 'Ê∏∏ÊàèÁéã,ÁîüÂëΩÂÄºÁªüËÆ°,ÂÜ≥ÊñóËÆ∞ÂΩï,Âç°ÁâåÊ∏∏Êàè,ÁîüÂëΩÂÄºËÆ°ÁÆóÂô®' }
    ]
  },
  data() {
    return {
      players: [],
      customAmount: 500,
      quickAmounts: [100, 500, 1000, 2000],
      gameStartTime: null,
      gameDuration: 0,
      totalTurns: 0,
      gameHistory: [],
      showHistory: false,
      showSettings: false,
      
      settings: {
        initialLife: 8000,
        lowLifeThreshold: 1000,
        soundEnabled: true
      },
      
      durationTimer: null
    }
  },
  computed: {
    quickAmountsStr: {
      get() {
        return this.quickAmounts.join(',')
      },
      set(value) {
        const amounts = value.split(',').map(v => parseInt(v.trim())).filter(v => !isNaN(v) && v > 0)
        if (amounts.length > 0) {
          this.quickAmounts = amounts.slice(0, 6) // ÊúÄÂ§ö6‰∏™Âø´Êç∑Êï∞ÂÄº
        }
      }
    }
  },
  mounted() {
    this.loadGameData()
    this.loadSettings()
    this.loadGameHistory()
    
    if (this.players.length === 0) {
      this.startNewGame()
    } else {
      this.startDurationTimer()
    }
  },
  beforeDestroy() {
    if (this.durationTimer) {
      clearInterval(this.durationTimer)
    }
  },
  methods: {
    startNewGame() {
      this.players = [
        this.createPlayer('Áé©ÂÆ∂1'),
        this.createPlayer('Áé©ÂÆ∂2')
      ]
      this.gameStartTime = new Date()
      this.gameDuration = 0
      this.totalTurns = 0
      this.startDurationTimer()
      this.saveGameData()
    },
    
    createPlayer(name) {
      return {
        name: name,
        currentLife: this.settings.initialLife,
        initialLife: this.settings.initialLife,
        history: [],
        wins: 0,
        losses: 0
      }
    },
    
    resetCurrentGame() {
      this.players.forEach(player => {
        player.currentLife = player.initialLife
        player.history = []
      })
      this.gameStartTime = new Date()
      this.gameDuration = 0
      this.totalTurns = 0
      this.startDurationTimer()
      this.saveGameData()
    },
    
    addPlayer() {
      if (this.players.length < 4) {
        this.players.push(this.createPlayer(`Áé©ÂÆ∂${this.players.length + 1}`))
        this.saveGameData()
      }
    },
    
    handlePlayerAction(playerIndex, command) {
      if (command === 'reset') {
        this.players[playerIndex].currentLife = this.players[playerIndex].initialLife
        this.players[playerIndex].history = []
        this.saveGameData()
      } else if (command === 'remove' && this.players.length > 2) {
        this.players.splice(playerIndex, 1)
        this.saveGameData()
      }
    },
    
    changeLife(playerIndex, amount) {
      const player = this.players[playerIndex]
      const oldLife = player.currentLife
      const newLife = Math.max(0, oldLife + amount)
      
      player.currentLife = newLife
      player.history.push({
        change: amount,
        oldLife: oldLife,
        newLife: newLife,
        time: new Date()
      })
      
      // ÈôêÂà∂ÂéÜÂè≤ËÆ∞ÂΩïÊï∞Èáè
      if (player.history.length > 50) {
        player.history = player.history.slice(-50)
      }
      
      this.totalTurns++
      
      // Ê£ÄÊü•Ê∏∏ÊàèÁªìÊùü
      if (newLife === 0) {
        this.handleGameEnd(playerIndex)
      }
      
      // ‰ΩéÁîüÂëΩÂÄºË≠¶Âëä
      if (newLife <= this.settings.lowLifeThreshold && newLife > 0) {
        this.playSound('warning')
      }
      
      this.saveGameData()
    },
    
    handleGameEnd(loserIndex) {
      const loser = this.players[loserIndex]
      loser.losses++
      
      // ÂÖ∂‰ªñÁé©ÂÆ∂Ëé∑ËÉú
      this.players.forEach((player, index) => {
        if (index !== loserIndex) {
          player.wins++
        }
      })
      
      // ‰øùÂ≠òÊ∏∏ÊàèËÆ∞ÂΩï
      this.saveGameToHistory()
      
      this.$message({
        message: `${loser.name} Ë¥•ÂåóÔºÅÊ∏∏ÊàèÁªìÊùüÔºÅ`,
        type: 'warning',
        duration: 5000
      })
      
      this.playSound('gameEnd')
    },
    
    saveGameToHistory() {
      const gameRecord = {
        id: Date.now(),
        startTime: this.gameStartTime,
        endTime: new Date(),
        duration: this.gameDuration,
        players: this.players.map(player => ({
          name: player.name,
          finalLife: player.currentLife,
          initialLife: player.initialLife,
          totalChanges: player.history.length
        })),
        totalTurns: this.totalTurns
      }
      
      this.gameHistory.unshift(gameRecord)
      
      // ÈôêÂà∂ÂéÜÂè≤ËÆ∞ÂΩïÊï∞Èáè
      if (this.gameHistory.length > 20) {
        this.gameHistory = this.gameHistory.slice(0, 20)
      }
      
      this.saveGameHistory()
    },
    
    deleteGameHistory(index) {
      this.gameHistory.splice(index, 1)
      this.saveGameHistory()
    },
    
    clearAllHistory() {
      this.$confirm('Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤ËÆ∞ÂΩïÂêóÔºü', 'Á°ÆËÆ§', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(() => {
        this.gameHistory = []
        this.saveGameHistory()
        this.$message.success('ÂéÜÂè≤ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫')
      })
    },
    
    startDurationTimer() {
      if (this.durationTimer) {
        clearInterval(this.durationTimer)
      }
      
      this.durationTimer = setInterval(() => {
        if (this.gameStartTime) {
          this.gameDuration = Math.floor((new Date() - this.gameStartTime) / 1000)
        }
      }, 1000)
    },
    
    getLifeClass(life) {
      if (life === 0) return 'zero'
      if (life <= this.settings.lowLifeThreshold) return 'low'
      if (life >= this.settings.initialLife * 0.8) return 'high'
      return 'normal'
    },
    
    getLifePercentage(life) {
      return Math.max(0, Math.min(100, (life / this.settings.initialLife) * 100))
    },
    
    getLifeStatus(life) {
      if (life === 0) return 'exception'
      if (life <= this.settings.lowLifeThreshold) return 'exception'
      if (life <= this.settings.initialLife * 0.3) return 'warning'
      return 'success'
    },
    
    playSound(type) {
      if (!this.settings.soundEnabled) return

      // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†Èü≥ÊïàÊí≠ÊîæÈÄªËæë
      // Áî±‰∫éÊòØÁ∫ØÂâçÁ´ØÔºåÂèØ‰ª•‰ΩøÁî®Web Audio APIÊàñÈ¢ÑËÆæÁöÑÈü≥È¢ëÊñá‰ª∂
      console.log('Playing sound:', type)
    },
    
    formatTime(date) {
      return new Date(date).toLocaleTimeString('zh-CN', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      })
    },
    
    formatDateTime(date) {
      return new Date(date).toLocaleString('zh-CN')
    },
    
    formatDuration(seconds) {
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const secs = seconds % 60
      
      if (hours > 0) {
        return `${hours}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
      } else {
        return `${minutes}:${secs.toString().padStart(2, '0')}`
      }
    },
    
    saveSettings() {
      localStorage.setItem('yugiohLifeSettings', JSON.stringify(this.settings))
      this.showSettings = false
      this.$message.success('ËÆæÁΩÆÂ∑≤‰øùÂ≠ò')
    },
    
    loadSettings() {
      const saved = localStorage.getItem('yugiohLifeSettings')
      if (saved) {
        this.settings = { ...this.settings, ...JSON.parse(saved) }
      }
    },
    
    resetSettings() {
      this.settings = {
        initialLife: 8000,
        lowLifeThreshold: 1000,
        soundEnabled: true
      }
    },
    
    saveGameData() {
      const gameData = {
        players: this.players,
        gameStartTime: this.gameStartTime,
        totalTurns: this.totalTurns
      }
      localStorage.setItem('yugiohLifeGame', JSON.stringify(gameData))
    },
    
    loadGameData() {
      const saved = localStorage.getItem('yugiohLifeGame')
      if (saved) {
        const gameData = JSON.parse(saved)
        this.players = gameData.players || []
        this.gameStartTime = gameData.gameStartTime ? new Date(gameData.gameStartTime) : null
        this.totalTurns = gameData.totalTurns || 0
      }
    },
    
    saveGameHistory() {
      localStorage.setItem('yugiohLifeHistory', JSON.stringify(this.gameHistory))
    },
    
    loadGameHistory() {
      const saved = localStorage.getItem('yugiohLifeHistory')
      if (saved) {
        this.gameHistory = JSON.parse(saved)
      }
    }
  }
}
</script>

